// Test file for Medical Event API endpoints
// This file is used to test the connection to backend API endpoints

import { 
  searchStudents, 
  getStudentMedicalInfo, 
  createMedicalIncident,
  getMedicalIncidents,
  getMedicalIncidentById,
  notifyParent 
} from './apiClient';

// Test search students
export const testSearchStudents = async (query: string = 'test') => {
  console.log('=== Testing searchStudents ===');
  try {
    const response = await searchStudents(query);
    console.log('✅ Search students successful:', response);
    return response;
  } catch (error: any) {
    console.error('❌ Search students failed:', error.response?.data || error.message);
    throw error;
  }
};

// Test get student medical info
export const testGetStudentMedicalInfo = async (studentId: string = '1') => {
  console.log('=== Testing getStudentMedicalInfo ===');
  try {
    const response = await getStudentMedicalInfo(studentId);
    console.log('✅ Get student medical info successful:', response);
    return response;
  } catch (error: any) {
    console.error('❌ Get student medical info failed:', error.response?.data || error.message);
    throw error;
  }
};

