// Run all tests
export const runAllMedicalEventTests = async () => {
  console.log('🚀 Starting Medical Event API Tests...');
  
  // Test 1: Search students
  try {
    await testSearchStudents('nguyen');
  } catch (error) {
    console.log('Search students test failed, but continuing...');
  }
  
  // Test 2: Get medical incidents
  try {
    await testGetMedicalIncidents();
  } catch (error) {
    console.log('Get medical incidents test failed, but continuing...');
  }
  
  // Test 3: Get medical incidents with filters
  try {
    await testGetMedicalIncidents({ studentId: '1' });
  } catch (error) {
    console.log('Get medical incidents with filters test failed, but continuing...');
  }
  
  // Test 4: Get student medical info
  try {
    await testGetStudentMedicalInfo('1');
  } catch (error) {
    console.log('Get student medical info test failed, but continuing...');
  }
  
  // Test 5: Get medical incident by ID
  try {
    await testGetMedicalIncidentById('1');
  } catch (error) {
    console.log('Get medical incident by ID test failed, but continuing...');
  }
  
  console.log('✅ All Medical Event API tests completed!');
};

// Sample data for testing create medical incident
export const sampleMedicalIncidentData = {
  studentId: "1",
  incidentType: "injury",
  description: "Test medical incident",
  severity: "medium",
  symptoms: ["headache", "fever"],
  medications: ["paracetamol"],
  notes: "This is a test incident",
  timestamp: new Date().toISOString()
};

// Export for use in console or other files
(window as any).testMedicalEventAPIs = {
  testSearchStudents,
  testGetStudentMedicalInfo,
  testGetMedicalIncidents,
  testCreateMedicalIncident,
  testGetMedicalIncidentById,
  testNotifyParent,
  runAllMedicalEventTests,
  sampleMedicalIncidentData
};
