// API Status Summary Component
import React from 'react';
import { 
  Card, 
  CardContent, 
  Typography, 
  Box, 
  Chip,
  Grid
} from '@mui/material';
import { 
  CheckCircle as SuccessIcon,
  Error as ErrorIcon,
  Info as InfoIcon
} from '@mui/icons-material';

interface ApiStatusSummaryProps {
  show: boolean;
  onClose: () => void;
}

const ApiStatusSummary: React.FC<ApiStatusSummaryProps> = ({ show, onClose }) => {
  if (!show) return null;

  const apiStatuses = [
    { name: 'Search Students', status: 'success', endpoint: '/api/student/search-by-name' },
    { name: 'Get Medical Incidents', status: 'success', endpoint: '/api/medical-event' },
    { name: 'Get Student Medical Info', status: 'success', endpoint: '/api/medical-event/student/{id}' },
    { name: 'Get Medical Incident by ID', status: 'warning', endpoint: '/api/medical-event/{id}', note: '404 - No data' },
  ];

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'success': return <SuccessIcon color="success" />;
      case 'error': return <ErrorIcon color="error" />;
      case 'warning': return <InfoIcon color="warning" />;
      default: return <InfoIcon />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'success': return 'success';
      case 'error': return 'error';
      case 'warning': return 'warning';
      default: return 'default';
    }
  };

  return (
    <Card sx={{ mt: 2, border: '1px solid #e0e0e0' }}>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
          <Typography variant="h6">🔗 API Connection Status</Typography>
          <Typography 
            variant="body2" 
            sx={{ cursor: 'pointer', color: 'text.secondary' }}
            onClick={onClose}
          >
            ✕
          </Typography>
        </Box>
        
        <Grid container spacing={2}>
          {apiStatuses.map((api, index) => (
            <Grid item xs={12} md={6} key={index}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, p: 1, border: '1px solid #f0f0f0', borderRadius: 1 }}>
                {getStatusIcon(api.status)}
                <Box sx={{ flex: 1 }}>
                  <Typography variant="body2" fontWeight="medium">
                    {api.name}
                  </Typography>
                  <Typography variant="caption" color="text.secondary">
                    {api.endpoint}
                  </Typography>
                  {api.note && (
                    <Typography variant="caption" color="warning.main" display="block">
                      {api.note}
                    </Typography>
                  )}
                </Box>
                <Chip 
                  label={api.status} 
                  size="small" 
                  color={getStatusColor(api.status) as any}
                  variant="outlined"
                />
              </Box>
            </Grid>
          ))}
        </Grid>

        <Box sx={{ mt: 2, p: 2, bgcolor: 'success.light', borderRadius: 1 }}>
          <Typography variant="body2" color="success.dark">
            ✅ <strong>Backend Connection:</strong> Successfully connected to localhost:5000
          </Typography>
          <Typography variant="caption" color="success.dark">
            Most APIs are working correctly. 404 errors are normal when no data exists.
          </Typography>
        </Box>
      </CardContent>
    </Card>
  );
};

export default ApiStatusSummary;
